"use client";

import * as React from "react";
import { DashboardLayout } from "@/components/dashboard/layout";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import { Switch } from "@/components/ui/switch";
import {
  Plus,
  Search,
  Filter,
  Download,
  MoreVertical,
  Edit,
  Copy,
  QrCode,
  Trash2,
  ChevronLeft,
  ChevronRight,
} from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetDescription,
} from "@/components/ui/sheet";
import axiosClient from "@/lib/axiosClient";
import { useToast } from "@/components/ui/use-toast";

type Category = {
  id: number;
  name: string;
  children?: Category[];
};

type Product = {
  id: number;
  image: string;
  name: string;
  sku: string;
  category: string;
  categoryId: number;
  price: string;
  scans: number;
  views: number;
  leads: number;
  status: boolean;
};

export default function ProductsPage() {
  const [products, setProducts] = React.useState<Product[]>([]);
  const [categories, setCategories] = React.useState<Category[]>([]);

  const [selectedProducts, setSelectedProducts] = React.useState<string[]>([]);
  const [searchQuery, setSearchQuery] = React.useState("");
  const [categoryFilter, setCategoryFilter] = React.useState("all");
  const [statusFilter, setStatusFilter] = React.useState("all");

  const [page, setPage] = React.useState(1);
  const [perPage, setPerPage] = React.useState(10);
  const [totalPages, setTotalPages] = React.useState(1);
  const [totalItems, setTotalItems] = React.useState(0);

  const [editDrawerOpen, setEditDrawerOpen] = React.useState(false);
  const [editingProduct, setEditingProduct] = React.useState<Product | null>(null);

  const { toast } = useToast();

  const allSelected = selectedProducts.length === products.length && products.length > 0;
  const someSelected = selectedProducts.length > 0 && !allSelected;

  //  LOAD CATEGORIES
  const loadCategories = async () => {
    try {
      const res = await axiosClient.get("/product-categories");
      if (res.data.success) setCategories(res.data.data || []);
    } catch (err: any) {
      toast({
        title: "Error",
        description: "Failed to load categories",
        variant: "destructive",
      });
    }
  };

  //  SERVER SIDE FILTER + PAGINATION
  const fetchProducts = async () => {
    try {
      const payload = {
        search: searchQuery,
        category_id: categoryFilter === "all" ? null : Number(categoryFilter),
        status:
          statusFilter === "all"
            ? null
            : statusFilter === "active"
            ? 1
            : 0,
        page,
        per_page: perPage,
      };

      const res = await axiosClient.post("/products/filter", payload);
      const apiData = res.data.data;

      const productData: Product[] = apiData.data.map((item: any) => ({
        id: item.id,
        image: item.images?.[0] || "/placeholder-product-image.jpg",
        name: item.name,
        sku: item.sku || "N/A",
        category: item.category?.name || "Uncategorized",
        categoryId: item.category?.id || 0,
        price: item.price ? `$${parseFloat(item.price).toFixed(2)}` : "$0.00",
        scans: item.scans || 0,
        views: item.views || 0,
        leads: item.leads || 0,
        status: item.is_active ?? true,
      }));

      setProducts(productData);
      setPage(apiData.current_page);
      setTotalPages(apiData.last_page);
      setTotalItems(apiData.total);
    } catch (e) {
      toast({
        title: "Error",
        description: "Failed to load products",
        variant: "destructive",
      });
    }
  };

  React.useEffect(() => {
    fetchProducts();
  }, [searchQuery, categoryFilter, statusFilter, page, perPage]);

  React.useEffect(() => {
    loadCategories();
  }, []);

  const toggleAll = () => {
    setSelectedProducts(allSelected ? [] : products.map((p) => p.id.toString()));
  };

  const toggleProduct = (id: number) => {
    const idStr = id.toString();
    setSelectedProducts((prev) =>
      prev.includes(idStr) ? prev.filter((p) => p !== idStr) : [...prev, idStr]
    );
  };

  const openEditDrawer = (product: Product) => {
    setEditingProduct(product);
    setEditDrawerOpen(true);
  };

  const renderCategoryOptions = (items: Category[], level = 0): React.ReactNode[] =>
    items.flatMap((cat) => [
      <SelectItem key={`${cat.id}-${level}`} value={cat.id.toString()}>
        {" ".repeat(level)} {cat.name}
      </SelectItem>,
      ...(cat.children ? renderCategoryOptions(cat.children, level + 1) : []),
    ]);

  const startItem = (page - 1) * perPage + 1;
  const endItem = Math.min(page * perPage, totalItems);

  return (
    <DashboardLayout>
      <div className="space-y-6">

        {/*  FILTER BAR */}
        <div className="flex gap-4">
          <Input
            placeholder="Search..."
            value={searchQuery}
            onChange={(e) => {
              setSearchQuery(e.target.value);
              setPage(1);
            }}
          />

          <Select value={categoryFilter} onValueChange={(v) => { setCategoryFilter(v); setPage(1); }}>
            <SelectTrigger><SelectValue placeholder="Category" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All</SelectItem>
              {renderCategoryOptions(categories)}
            </SelectContent>
          </Select>

          <Select value={statusFilter} onValueChange={(v) => { setStatusFilter(v); setPage(1); }}>
            <SelectTrigger><SelectValue placeholder="Status" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All</SelectItem>
              <SelectItem value="active">Active</SelectItem>
              <SelectItem value="inactive">Inactive</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/*  TABLE */}
        <div className="rounded-xl border bg-card shadow-sm overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>
                  <Checkbox
                    checked={allSelected ? true : someSelected ? "indeterminate" : false}
                    onCheckedChange={toggleAll}
                  />
                </TableHead>
                <TableHead>Name</TableHead>
                <TableHead>Category</TableHead>
                <TableHead>Price</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>

            <TableBody>
              {products.map((p) => (
                <TableRow key={p.id}>
                  <TableCell>
                    <Checkbox checked={selectedProducts.includes(p.id.toString())}
                      onCheckedChange={() => toggleProduct(p.id)} />
                  </TableCell>
                  <TableCell>{p.name}</TableCell>
                  <TableCell>{p.category}</TableCell>
                  <TableCell>{p.price}</TableCell>
                  <TableCell><Switch checked={p.status} /></TableCell>
                  <TableCell>
                    <Button size="sm" onClick={() => openEditDrawer(p)}>Edit</Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>

        {/*  PAGINATION */}
        <div className="flex justify-between items-center">
          <span>Showing {startItem}-{endItem} of {totalItems}</span>

          <div className="flex gap-2">
            <Button disabled={page <= 1} onClick={() => setPage(page - 1)}>
              <ChevronLeft />
            </Button>

            <Button disabled={page >= totalPages} onClick={() => setPage(page + 1)}>
              <ChevronRight />
            </Button>
          </div>
        </div>

      </div>
    </DashboardLayout>
  );
}
